import * as React from "react";
import {useEffect, useState} from "react";
import {useQueryParam} from "use-query-params";
import {
  Col,
  Container,
  Nav,
  NavItem,
  NavLink,
  Row,
  TabContent,
  TabPane,
} from "reactstrap";
import {SearchForm} from "~/components/SearchForm";
import {DocumentIndex} from "~/applications/documents/DocumentIndex";
import {Document} from "~/models/Document";
import {DocumentsByAuthorTable} from "~/applications/documents/DocumentsByAuthorTable";
import {DocumentsByDateTable} from "~/applications/documents/DocumentsByDateTable";

type TabKey = "author" | "datePublished";

export const DocumentsApplication: React.FunctionComponent<{
  readonly documentIndex: DocumentIndex;
}> = ({documentIndex}) => {
  const [activeTabKeyQueryParam, setActiveTabKey] =
    useQueryParam<TabKey>("tab");
  const activeTabKey: TabKey = activeTabKeyQueryParam ?? "author";
  const [documents, setDocuments] = useState<readonly Document[] | null>(null);
  const [query, setQuery] = useQueryParam<string | undefined>("query");

  useEffect(() => {
    if (query) {
      setDocuments(documentIndex.search(query));
    } else {
      setDocuments(documentIndex.documents);
    }
  }, [documentIndex, query]);

  if (!documents) {
    return null;
  }

  const tabs: {content: React.ReactNode; key: TabKey; title: string}[] = [];
  tabs.push({
    key: "author",
    title: "By department",
    content: <DocumentsByAuthorTable documents={documents} />,
  });
  tabs.push({
    key: "datePublished",
    title: "By date",
    content: <DocumentsByDateTable documents={documents} />,
  });

  return (
    <Container fluid>
      <Row>
        <Col className="ms-auto" xs={4}>
          <SearchForm onSearch={setQuery} />
        </Col>
      </Row>
      <Row>
        <Col className="px-0">
          <Nav tabs>
            {tabs.map((tab) => (
              <NavItem key={tab.key}>
                <NavLink
                  className={activeTabKey === tab.key ? "active" : undefined}
                  onClick={() => setActiveTabKey(tab.key)}
                  style={{cursor: "pointer", fontSize: "small"}}
                >
                  {tab.title}
                </NavLink>
              </NavItem>
            ))}
          </Nav>
          <TabContent activeTab={activeTabKey}>
            {tabs.map((tab) => (
              <TabPane key={tab.key} tabId={tab.key}>
                {tab.content}
              </TabPane>
            ))}
          </TabContent>
        </Col>
      </Row>
    </Container>
  );
};
