import * as React from "react";
import {Article} from "~/models/Article";
import {Table} from "reactstrap";

export const ArticlesTable: React.FunctionComponent<{
  readonly articles: readonly Article[];
}> = ({articles}) => {
  const includeThumbnail = articles.some((article) => article.thumbnailUrl);

  return (
    <Table bordered striped>
      <tbody>
        {articles
          .concat()
          .sort(
            (left, right) =>
              left.datePublished.getTime() - right.datePublished.getTime()
          )
          .map((article) => (
            <tr key={article.id}>
              <td className="text-center">
                {article.datePublished.toLocaleDateString()}
              </td>
              {includeThumbnail ? (
                <td className="text-center">
                  {article.thumbnailUrl ? (
                    <img
                      src={article.thumbnailUrl}
                      style={{maxHeight: 160, maxWidth: 160}}
                      title="article thumbnail"
                    />
                  ) : null}
                </td>
              ) : null}
              <td>
                <strong>{article.headline}</strong>
                {article.abstract ? <p>{article.abstract}</p> : null}
              </td>
            </tr>
          ))}
      </tbody>
    </Table>
  );
};
