import * as React from "react";
import {Document} from "~/models/Document";
import {Table} from "reactstrap";

export const DocumentsByAuthorTable: React.FunctionComponent<{
  documents: readonly Document[];
}> = ({documents}) => {
  const documentsByAuthor: {[index: string]: Document[]} = documents.reduce(
    (map, document) => {
      const documentAuthor = document.author ?? "Miscellaneous";
      const documentsWithAuthor = map[documentAuthor];
      if (documentsWithAuthor) {
        documentsWithAuthor.push(document);
      } else {
        map[documentAuthor] = [document];
      }
      return map;
    },
    {} as {[index: string]: Document[]}
  );

  return (
    <Table style={{borderCollapse: "collapse"}}>
      {Object.keys(documentsByAuthor)
        .sort()
        .map((author) => (
          <tbody key={author} style={{borderTop: "24px solid white"}}>
            <tr>
              <td colSpan={2}>
                <h6>{author}</h6>
              </td>
            </tr>
            {documentsByAuthor[author]!.sort(
              (left, right) =>
                right.sortDate.getTime() - left.sortDate.getTime()
            ).map((document) => (
              <tr key={document.id}>
                <td>{document.displayDate?.toLocaleDateString()}</td>
                <td>
                  {document.url ? (
                    <a href={document.url}>{document.titleOrId}</a>
                  ) : (
                    <span>{document.titleOrId}</span>
                  )}
                </td>
              </tr>
            ))}
          </tbody>
        ))}
    </Table>
  );
};
