import * as React from "react";
import {Document} from "~/models/Document";
import {Table} from "reactstrap";

export const DocumentsByDateTable: React.FunctionComponent<{
  documents: readonly Document[];
}> = ({documents}) => {
  const documentsByYear: {[index: number]: Document[]} = documents.reduce(
    (map, document) => {
      const documentYear = document.displayDate?.getFullYear() ?? 0;
      const documentsWithYear = map[documentYear];
      if (documentsWithYear) {
        documentsWithYear.push(document);
      } else {
        map[documentYear] = [document];
      }
      return map;
    },
    {} as {[index: number]: Document[]}
  );

  return (
    <Table style={{borderCollapse: "collapse"}}>
      {Object.keys(documentsByYear)
        .map(parseInt)
        .sort((left, right) => right - left)
        .map((year) => (
          <tbody key={year} style={{borderTop: "24px solid white"}}>
            <tr>
              <td colSpan={2}>
                <h6>{year !== 0 ? year : "Unknown"}</h6>
              </td>
            </tr>
            {documentsByYear[year]!.sort(
              (left, right) =>
                right.sortDate.getTime() - left.sortDate.getTime()
            ).map((document) => (
              <tr key={document.id}>
                <td>{document.displayDate?.toLocaleDateString()}</td>
                <td>
                  {document.url ? (
                    <a href={document.url}>{document.titleOrId}</a>
                  ) : (
                    <span>{document.titleOrId}</span>
                  )}
                </td>
              </tr>
            ))}
          </tbody>
        ))}
    </Table>
  );
};
