import * as React from "react";
import * as ReactDOM from "react-dom";
import {QueryParamProvider} from "use-query-params";
import {BrowserRouter as Router, Route} from "react-router-dom";
import {DocumentsApplication} from "~/applications/documents/DocumentsApplication";
import {DocumentIndex} from "~/applications/documents/DocumentIndex";
import {ResourcesApplication} from "~/applications/resources/ResourcesApplication";
import {ResourceIndex} from "~/applications/resources/ResourceIndex";
import {ArticlesApplication} from "~/applications/articles/ArticlesApplication";
import {articlesJsonSchema} from "~/models/json/articlesJsonSchema";
import {Article} from "~/models/Article";

const ApplicationWrapper: React.FunctionComponent<
  React.PropsWithChildren<{}>
> = ({children}) => (
  <Router>
    <QueryParamProvider ReactRouterRoute={Route}>
      {children}{" "}
    </QueryParamProvider>
  </Router>
);

let applicationElement: HTMLElement | null = null;
if ((applicationElement = document.getElementById("towndex-articles"))) {
  fetch("/data/articles.json").then((response) => {
    response.json().then((responseJson) => {
      const articlesJson = articlesJsonSchema.validateSync(responseJson);
      const articles = articlesJson.models
        .map((articleJson) => new Article(articleJson))
        .filter((article) => !article.expired);
      ReactDOM.render(
        <ApplicationWrapper>
          <ArticlesApplication articles={articles} />
        </ApplicationWrapper>,
        applicationElement
      );
    });
  });
} else if (
  (applicationElement = document.getElementById("towndex-documents"))
) {
  fetch("/data/documents.json").then((response) => {
    response.json().then((responseJson) => {
      const documentIndex = DocumentIndex.fromJson(responseJson);
      ReactDOM.render(
        <ApplicationWrapper>
          <DocumentsApplication documentIndex={documentIndex} />
        </ApplicationWrapper>,
        applicationElement
      );
    });
  });
} else if (
  (applicationElement = document.getElementById("towndex-resources"))
) {
  fetch("/data/resources.json").then((response) => {
    response.json().then((responseJson) => {
      ReactDOM.render(
        <ApplicationWrapper>
          <ResourcesApplication
            resourceIndex={ResourceIndex.fromJson(responseJson)}
          />
        </ApplicationWrapper>,
        applicationElement
      );
    });
  });
}
