import {RootModel} from "~/models/RootModel";
import {ArticleJson} from "~/models/json/ArticleJson";

export class Article extends RootModel {
  constructor(private readonly json: ArticleJson) {
    super();
  }

  get abstract(): string | undefined {
    return this.json.abstract;
  }

  get datePublished(): Date {
    return this.json.datePublished;
  }

  get expired(): boolean {
    return this.expires <= new Date();
  }

  get expires(): Date {
    return this.json.expires;
  }

  get headline(): string {
    return this.json.headline;
  }

  get id(): string {
    return this.json.id;
  }

  get thumbnailUrl(): string | undefined {
    return this.json.thumbnailUrl;
  }

  get url(): string {
    return this.json.url;
  }
}
