import {DocumentJson} from "~/models/json/DocumentJson";
import {documentJsonSchema} from "~/models/json/documentJsonSchema";
import {RootModel} from "~/models/RootModel";

export class Document extends RootModel {
  constructor(private readonly json: DocumentJson) {
    super();
  }

  get abstract(): string | undefined {
    return this.json.abstract;
  }

  get author(): string | undefined {
    return this.json.author ?? this.json.metadata?.author;
  }

  get datePublished(): Date | undefined {
    return this.json.datePublished;
  }

  get displayDate(): Date | undefined {
    if (this.datePublished) {
      return this.datePublished;
    } else if (this.temporalCoverage) {
      return this.temporalCoverage;
    } else {
      return undefined;
    }
  }

  get id(): string {
    return this.json.id;
  }

  get keywords(): readonly string[] {
    return this.json.keywords ?? [];
  }

  static fromJson(json: any): Document {
    return new Document(documentJsonSchema.validateSync(json));
  }

  get namedEntitiesByLabel(): {[index: string]: readonly string[]} {
    return this.json.namedEntitiesByLabel ?? {};
  }

  get sortDate(): Date {
    return this.displayDate ?? new Date(0);
  }

  get temporalCoverage(): Date | undefined {
    return this.json.temporalCoverage;
  }

  get title(): string | undefined {
    return this.json.title ?? this.json.metadata?.title;
  }

  get titleOrId(): string | undefined {
    return this.title ?? this.id;
  }

  get url(): string {
    return this.json.url;
  }
}
